/**
* @description : This is the main ui for @class Login
*
* @method render
* @method renderMainUI
* @method renderLoginButton
* @method renderHelperComponents
*
* @author Armin Eslami
* @since File available since Release 1.0.0
* @version 1.0.0
*
*/
import React from 'react';
import { Row, Col } from 'react-flexbox-grid-aphrodite';
import { Card } from 'material-ui/Card';
import TextField from 'material-ui/TextField';
import Checkbox from 'material-ui/Checkbox';
import SvgIcon from 'material-ui/SvgIcon';
import FloatingActionButton from 'material-ui/FloatingActionButton';
import RaisedButton from 'material-ui/RaisedButton';
import LinearProgress from 'material-ui/LinearProgress';
import CircularProgress from 'material-ui/CircularProgress';
import Snackbar from 'material-ui/Snackbar';
import Dialog from 'material-ui/Dialog';
import FlatButton from 'material-ui/FlatButton';

import BaseComponent from 'components/base/BaseComponent';
import { Global as GlobalStrings, Login as LoginStrings } from 'i18n/Persian';
import Logo from 'components/Logo';

const Account = props =>
  <SvgIcon {...props}>
    <path d="M12,4A4,4 0 0,1 16,8A4,4 0 0,1 12,12A4,4 0 0,1 8,8A4,4 0 0,1 12,4M12,14C16.42,14 20,15.79 20,18V20H4V18C4,15.79 7.58,14 12,14Z" />
  </SvgIcon>;

const Lock = props =>
  <SvgIcon {...props}>
    <path d="M12,17A2,2 0 0,0 14,15C14,13.89 13.1,13 12,13A2,2 0 0,0 10,15A2,2 0 0,0 12,17M18,8A2,2 0 0,1 20,10V20A2,2 0 0,1 18,22H6A2,2 0 0,1 4,20V10C4,8.89 4.9,8 6,8H7V6A5,5 0 0,1 12,1A5,5 0 0,1 17,6V8H18M12,3A3,3 0 0,0 9,6V8H15V6A3,3 0 0,0 12,3Z" />
  </SvgIcon>;

const Email = props =>
  <SvgIcon {...props}>
    <path d="M20,8L12,13L4,8V6L12,11L20,6M20,4H4C2.89,4 2,4.89 2,6V18A2,2 0 0,0 4,20H20A2,2 0 0,0 22,18V6C22,4.89 21.1,4 20,4Z" />
  </SvgIcon>;

const Eye = props =>
  <SvgIcon {...props}>
    <path d="M12,9A3,3 0 0,0 9,12A3,3 0 0,0 12,15A3,3 0 0,0 15,12A3,3 0 0,0 12,9M12,17A5,5 0 0,1 7,12A5,5 0 0,1 12,7A5,5 0 0,1 17,12A5,5 0 0,1 12,17M12,4.5C7,4.5 2.73,7.61 1,12C2.73,16.39 7,19.5 12,19.5C17,19.5 21.27,16.39 23,12C21.27,7.61 17,4.5 12,4.5Z" />
  </SvgIcon>;

const EyeOff = props =>
  <SvgIcon {...props}>
    <path d="M11.83,9L15,12.16C15,12.11 15,12.05 15,12A3,3 0 0,0 12,9C11.94,9 11.89,9 11.83,9M7.53,9.8L9.08,11.35C9.03,11.56 9,11.77 9,12A3,3 0 0,0 12,15C12.22,15 12.44,14.97 12.65,14.92L14.2,16.47C13.53,16.8 12.79,17 12,17A5,5 0 0,1 7,12C7,11.21 7.2,10.47 7.53,9.8M2,4.27L4.28,6.55L4.73,7C3.08,8.3 1.78,10 1,12C2.73,16.39 7,19.5 12,19.5C13.55,19.5 15.03,19.2 16.38,18.66L16.81,19.08L19.73,22L21,20.73L3.27,3M12,7A5,5 0 0,1 17,12C17,12.64 16.87,13.26 16.64,13.82L19.57,16.75C21.07,15.5 22.27,13.86 23,12C21.27,7.61 17,4.5 12,4.5C10.6,4.5 9.26,4.75 8,5.2L10.17,7.35C10.74,7.13 11.35,7 12,7Z" />
  </SvgIcon>;

const LeftArrow = props =>
  <SvgIcon {...props}>
    <path d="M20,10V14H11L14.5,17.5L12.08,19.92L4.16,12L12.08,4.08L14.5,6.5L11,10H20Z" />
  </SvgIcon>;

export class LoginUI extends BaseComponent {
  render() {
    return (
      <div className={this.props.states.rootClassName}>
        <Row
          style={{ padding: '32px 0', direction: 'rtl' }}
          center="xs"
          className="bottom-to-top-move"
        >
          <Col lg={4} md={4} sm={6} xs={11}>
            <Card style={{ position: 'relative' }}>
              {/*Show LinearProgress if user is not using small screen device*/}
              {this.props.states.isLoading && this.props.states.windowWidth > 768
                ? <LinearProgress
                    mode="indeterminate"
                    style={{ borderRadius: 'none' }}
                  />
                : void 0}

              <Logo
                width={72}
                height={72}
                style={{ paddingTop: 32, paddingBottom: 32 }}
              />

              {this.renderMainUI()}

              {/*Show Login button if isLoading is false*/}
              {!this.props.states.isLoading ? this.renderLoginButton() : void 0}
            
              {/*Show CircularProgress if user is using small screen device*/}
              {this.props.states.isLoading && this.props.states.windowWidth < 768
              ? <CircularProgress size={30} style={{ marginBottom: 52 }}/> 
              : void 0}

            </Card>
          </Col>
        </Row>

        {this.renderHelperComponents()}
      </div>
    );
  }

  renderMainUI = () => {
    return (
      <Row center="xs">
        <Col lg={6} md={6} sm={6} xs={8}>
          <div style={{ padding: 16, paddingBottom: 72, textAlign: 'center' }}>
            {/* Username input */}
            <div style={{ position: 'relative', display: 'inline-block' }}>
              <Account
                color={
                  this.props.states.username.length >=
                  this.props.data.usernameMinLength
                    ? '#00bcd4'
                    : '#000000'
                }
                style={{
                  position: 'absolute',
                  left: 0,
                  top: 'auto',
                  bottom: 16,
                  width: 20,
                  height: 20
                }}
              />
              <TextField
                value={this.props.states.username}
                floatingLabelText={LoginStrings.usernameInput}
                style={{
                  width: '100%',
                  fontFamily: 'iransans',
                  fontSize: '.9rem',
                  textIndent: 30
                }}
                inputStyle={{ paddingRight: 32 }}
                floatingLabelStyle={{
                  left: 0,
                  fontSize: '.8rem'
                }}
                ref="username"
                type="text"
                onChange={e => {
                  this.props.handlers.onUsernameChange(e.target.value);
                }}
                onKeyUp={e => {
                  e.key === 'Enter' ? this.props.handlers.onSubmit() : void 0;
                }}
              />
            </div>

            {!this.props.states.isRecovering
              ? // Password input
                <div style={{ position: 'relative', display: 'inline-block' }}>
                  <Lock
                    color={
                      this.props.states.password.length >=
                      this.props.data.passwordMinLength
                        ? '#00bcd4'
                        : '#000000'
                    }
                    style={{
                      position: 'absolute',
                      left: 0,
                      top: 'auto',
                      bottom: 16,
                      width: 20,
                      height: 20
                    }}
                  />
                  <TextField
                    value={this.props.states.password}
                    floatingLabelText={LoginStrings.passwordInput}
                    style={{
                      width: '100%',
                      fontFamily: 'iransans',
                      fontSize: '.9rem',
                      textIndent: 30
                    }}
                    inputStyle={{ paddingRight: 52 }}
                    floatingLabelStyle={{
                      left: 0,
                      fontSize: '.8rem'
                    }}
                    ref="password"
                    type={this.props.states.showPassword ? 'text' : 'password'}
                    onChange={e => {
                      this.props.handlers.onPasswordChange(e.target.value);
                    }}
                    onKeyUp={e => {
                      e.key === 'Enter'
                        ? this.props.handlers.onSubmit()
                        : void 0;
                    }}
                  />
                  {!this.props.states.showPassword
                    ? <Eye
                        color="#9E9E9E"
                        onTouchTap={
                          this.props.handlers.onPasswordShowToggleTouchTap
                        }
                        style={{
                          position: 'absolute',
                          right: 0,
                          top: 'auto',
                          bottom: 16,
                          width: 20,
                          height: 20,
                          cursor: 'pointer'
                        }}
                      />
                    : <EyeOff
                        color="#9E9E9E"
                        onTouchTap={
                          this.props.handlers.onPasswordShowToggleTouchTap
                        }
                        style={{
                          position: 'absolute',
                          right: 0,
                          top: 'auto',
                          bottom: 16,
                          width: 20,
                          height: 20,
                          cursor: 'pointer'
                        }}
                      />}
                </div>
              : // Email input
                <div>
                  <div
                    style={{ position: 'relative', display: 'inline-block' }}
                  >
                    <Email
                      color={
                        this.props.states.isEmailValid ? '#00bcd4' : '#000000'
                      }
                      style={{
                        position: 'absolute',
                        left: 0,
                        top: 'auto',
                        bottom: 16,
                        width: 20,
                        height: 20
                      }}
                    />
                    <TextField
                      value={this.props.states.email}
                      floatingLabelText={LoginStrings.emailInput}
                      style={{
                        width: '100%',
                        fontFamily: 'iransans',
                        fontSize: '.85rem',
                        textIndent: 30
                      }}
                      inputStyle={{ paddingRight: 32 }}
                      floatingLabelStyle={{
                        left: 0,
                        fontSize: '.8rem'
                      }}
                      ref="email"
                      type="email"
                      onChange={e => {
                        this.props.handlers.onEmailChange(e.target.value);
                      }}
                      onKeyUp={e => {
                        e.key === 'Enter'
                          ? this.props.handlers.onSubmit()
                          : void 0;
                      }}
                    />
                  </div>

                  <p style={{ fontSize: '.77rem', textAlign: 'right' }}>
                    {LoginStrings.passwordRecoverNote}
                  </p>
                </div>}

            {/* Remember me checkbox */}
            {!this.props.states.isRecovering
              ? <div>
                  <Checkbox
                    checked={this.props.states.rememberMe}
                    style={{
                      margin: 'auto',
                      width: 150,
                      textAlign: 'left',
                      marginTop: 32
                    }}
                    label={LoginStrings.rememberMe}
                    labelStyle={{ fontFamily: 'iransans', fontSize: '.85rem' }}
                    ref="rememberMeCheckbox"
                    onCheck={(e, isChecked) => {
                      this.props.handlers.onRememberMeChanged(isChecked);
                    }}
                  />
                </div>
              : void 0}

            {/* Login/Recover toggle */}
            {!this.props.states.isLoading
              ? <div
                  onTouchTap={this.props.handlers.onSectionToggleTouchTap}
                  style={{
                    width: 100,
                    position: 'absolute',
                    margin: 'auto',
                    left: 0,
                    right: 0,
                    bottom: 8,
                    cursor: 'pointer',
                    color: '#9E9E9E'
                  }}
                >
                  <p style={{ fontSize: '.75rem' }}>
                    {!this.props.states.isRecovering
                      ? LoginStrings.passwordRecover
                      : LoginStrings.back}
                  </p>
                </div>
              : void 0}
          </div>
        </Col>
      </Row>
    );
  };

  renderLoginButton = () => {
    if (this.props.states.windowWidth > 768) {
      return (
        <FloatingActionButton
          className={this.props.states.loginButtonClassNanme}
          style={{
            position: 'absolute',
            bottom: 32,
            right: '-28px'
          }}
          onTouchTap={this.props.handlers.onSubmit}
        >
          {!this.props.states.isRecovering ? <LeftArrow /> : <Email />}
        </FloatingActionButton>
      );
    } else {
      return (
        <Row center="xs">
          <Col lg={6} md={6} sm={6} xs={8}>
            <RaisedButton
              label={
                !this.props.states.isRecovering
                  ? LoginStrings.login
                  : LoginStrings.sendEmail
              }
              primary={true}
              style={{ width: '100%', marginBottom: 52 }}
              labelStyle={{ fontSize: '.9rem' }}
              onTouchTap={this.props.handlers.onSubmit}
            />
          </Col>
        </Row>
      );
    }
  };

  /**
    * @description : Renders some helper components like snacks, dialogs, ...
    *
    * @author Armin Eslami
    *
    * @return jsx element
    */
  renderHelperComponents = () => {
    return (
      <div>
        <Snackbar
          open={this.props.states.isSnackOpen}
          message={this.props.states.snackMessage}
          style={{ textAlign: 'center' }}
          ref="snackbar"
          autoHideDuration={3000}
          onRequestClose={this.props.handlers.onSnackClose}
        />

        {/* Auto log out dialog */}
        <Dialog
          title={LoginStrings.autoLogOut}
          style={{ direction: 'ltr' }}
          titleClassName="dialogTitle"
          bodyClassName="dialogBody"
          contentStyle={{
            width: this.props.states.windowWidth < 768 ? '90%' : '40%'
          }}
          actions={[
            <FlatButton
              label={GlobalStrings.close}
              primary={true}
              onClick={this.props.handlers.onAutoLogOutDialogClose}
            />
          ]}
          modal={false}
          open={this.props.states.autoLogOuted || false}
          onRequestClose={this.props.handlers.onAutoLogOutDialogClose}
        >
          {LoginStrings.autoLogOutDialogBody}
        </Dialog>
      </div>
    );
  };
}

export default LoginUI;



