import moment from 'moment-jalaali';

/**
* @description : Takes a timestamp and convert it to formatted date
*
* @author Armin Eslami
*
* @param timestamp (string) : The timestamp to be converted
* @param persianDigits (bool) : On true returned date has persian numbers in it
* @param dateFormat (string) : The desired format for returned date
*
* @return formatted date
*/
export function getFormattedDateFromTimestamp(
  timestamp,
  persianDigits = true,
  dateFormat = 'dddd jD jMMMM jYYYY - kk:mm'
) {
  if (timestamp !== void 0) {
    moment.loadPersian();

    try {
      return persianDigits
        ? convertEnDigits(moment.unix(timestamp).format(dateFormat))
        : moment.unix(timestamp).format(dateFormat);
    } catch (error) {
      return timestamp;
    }
  }
}

/**
* @description : Converts english numbers to persian
*
* @author Armin Eslami
*
* @param enString (String) : An string containing english numbers
*
* @return string containing persian numbers
*/
export function convertEnDigits(enString = '') {
  var newValue = '';

  if (typeof enString !== 'string') {
    enString = enString.toString();
  }

  for (var i = 0; i < enString.length; i++) {
    var ch = enString.charCodeAt(i);
    if (ch >= 48 && ch <= 57) {
      // european digit range
      var newChar = ch + 1584;
      var char = String.fromCharCode(newChar);
      char = char === '٥' ? '۵' : char;
      newValue = newValue + char;
    } else {
      newValue = newValue + String.fromCharCode(ch);
    }
  }
  return newValue;
}

/**
* @description : Gets numeric string and checks whether all characters are number or not
*
* @author Soorena Sanagoo
*
* @param digits(string)
*
* @return true if all character of digits are number
*/
export function isNumber(digits) {
  var engDigit = ['1', '2', '3', '4', '5', '6', '7', '8', '9', '0'];
  var faDigit = ['۱', '۲', '۳', '۴', '۵', '۶', '۷', '۸', '۹', '۰'];
  for (var i = 0; i < digits.length; i++) {
    if (
      engDigit.indexOf(digits[i]) === -1 &&
      faDigit.indexOf(digits[i]) === -1
    ) {
      return false;
    }
  }
  return true;
}

/**
* @description : validate mobile, this function doesnt check empty string
*
* @author Soorena Sanagoo
*
* @param phoneNumber(string)
*
* @return true if phoneNumber is correct
*/
const PHONENUMBER_LENGTH = 11;
export function validateMobile(phoneNumber) {
  if (phoneNumber.charAt(0) !== '0' && phoneNumber.charAt(1) !== '9') {
    if (phoneNumber.charAt(0) !== '۰' && phoneNumber.charAt(1) !== '۹') {
      return false;
    }
  }

  if (phoneNumber.length !== PHONENUMBER_LENGTH) {
    return false;
  }

  if (!isNumber(phoneNumber)) {
    return false;
  }

  return true;
}

/**
* @description :  Checks an email address against some regex to ensure its a valid one

* @author Armin Eslami
*
* @param email (string) : An email address to check it
*
* @return true if its valid || false if its not valid
*/
export function validateEmail(email) {
  var regex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return regex.test(email);
}

/**
* @description : Checks if given input is a valid english or persian string with no numbers in it
*
* @author Soorena Sanagoo
*
* @param _text(string)
*
* @return  true if _text only contain alphabet
*/
export function isAlphabet(_text) {
  var text = _text.toLowerCase();
  var isValid = false;
  var i = undefined;

  var validFaCharacters = [
    'ض',
    'ص',
    'ث',
    'ق',
    'ف',
    'غ',
    'ع',
    'ه',
    'خ',
    'ح',
    'ج',
    'چ',
    'ش',
    'س',
    'ی',
    'ب',
    'ل',
    'ا',
    'آ',
    'ت',
    'ن',
    'م',
    'ک',
    'گ',
    'ظ',
    'ط',
    'ز',
    'ر',
    'ذ',
    'د',
    'پ',
    'ژ',
    'و',
    'ء',
    'ئ'
  ];

  var validEngCharacters = [
    'q',
    'w',
    'e',
    'r',
    't',
    'y',
    'u',
    'i',
    'o',
    'p',
    'a',
    's',
    'd',
    'f',
    'm',
    'n',
    'b',
    'v',
    'c',
    'x',
    'z',
    'l',
    'k',
    'j',
    'h',
    'g'
  ];

  for (i = 0; i < text.length; i++) {
    if (
      validFaCharacters.indexOf(text[i]) === -1 &&
      validEngCharacters.indexOf(text[i]) === -1
    ) {
      return isValid;
    }
  }

  for (i = 0; i < text.length; i++) {
    if (validFaCharacters.indexOf(text[i]) === -1) {
      isValid = false;
      break;
    } else {
      isValid = true;
    }
  }

  if (isValid) {
    return isValid;
  }

  for (i = 0; i < text.length; i++) {
    if (validEngCharacters.indexOf(text[i]) === -1) {
      isValid = false;
      break;
    } else {
      isValid = true;
    }
  }

  return isValid;
}

/**
* @description : A bundle of helper functions to create a smooth scroll 
* to given id of an html element. To use this bundle, all is required is calling
* @function scrollTo with id of an element
*
* @author Armin Eslami
*/
export var smoothScroll = {
  timer: null,

  stop: function() {
    clearTimeout(this.timer);
  },

  /**
  * @description : Scrolls to the given id on the html DOM
  *
  * @author Armin Eslami
  *
  * @param id (string) : Id of an element on the html DOM
  * @param callback (func) : A callback which will be called when the scroll is done
  */
  scrollTo: function(id, callback) {
    var settings = {
      duration: 1000,
      easing: {
        outQuint: function(x, t, b, c, d) {
          return c * ((t = t / d - 1) * t * t * t * t + 1) + b;
        }
      }
    };
    var percentage;
    var startTime;
    var node = document.getElementById(id);
    var nodeTop = node.offsetTop;
    var nodeHeight = node.offsetHeight;
    var body = document.body;
    var html = document.documentElement;
    var height = Math.max(
      body.scrollHeight,
      body.offsetHeight,
      html.clientHeight,
      html.scrollHeight,
      html.offsetHeight
    );
    var windowHeight = window.innerHeight;
    var offset = window.pageYOffset;
    var delta = nodeTop - offset;
    var bottomScrollableY = height - windowHeight;
    var targetY =
      bottomScrollableY < delta
        ? bottomScrollableY - (height - nodeTop - nodeHeight + offset)
        : delta;

    startTime = Date.now();
    percentage = 0;

    if (this.timer) {
      clearInterval(this.timer);
    }

    function step() {
      var yScroll;
      var elapsed = Date.now() - startTime;

      if (elapsed > settings.duration) {
        clearTimeout(this.timer);
      }

      percentage = elapsed / settings.duration;

      if (percentage > 1) {
        clearTimeout(this.timer);

        if (callback) {
          callback();
        }
      } else {
        yScroll = settings.easing.outQuint(
          0,
          elapsed,
          offset,
          targetY,
          settings.duration
        );
        window.scrollTo(0, yScroll);
        this.timer = setTimeout(step, 10);
      }
    }

    this.timer = setTimeout(step, 10);
  }
};
/**
* @description : compare two object for equivalent
*
* @author Soorena Sanagoo
*
* @param firstObj(object)
*
* @param secondObj(object)

* @return if two object are equal return true otherwise false
*/
export function compareObject(firstObj, secondObj) {
  if (typeof firstObj !== 'object' || typeof secondObj !== 'object') {
    return false;
  } else {
    if (Object.keys(firstObj).length !== Object.keys(secondObj).length) {
      return false;
    } else {
      var different = false;

      for (var i = 0; i < Object.keys(firstObj).length; i++) {
        if (secondObj[Object.keys(firstObj)[i]] === undefined) {
          different = true;
          break;
        } else if (
          firstObj[Object.keys(firstObj)[i]] !==
          secondObj[Object.keys(firstObj)[i]]
        ) {
          different = true;
          break;
        }
      }

      if (different) {
        return false;
      } else {
        return true;
      }
    }
  }
}

/**
* @description : Creates a clone of given object and returns it
* NOTE: if a key's value is a fucntion, this method wont be good
* and will remove those keys. For this kind of object maybe cloneDeep function
* of lodash would be much better
*
* @author Armin Eslami
*
* @param obj (object) : An object to be cloned
*
* @return cloned object
*/
export function cloneObject(obj) {
  if (null == obj || 'object' !== typeof obj) return obj;
  return JSON.parse(JSON.stringify(obj));
}



