/**
* @description : This is the main component of login route and handles all the logics.
*
* @method handleUserNameChange
* @method handlePasswordChange
* @method handleEmailChange
* @method handleLogin
* @method handleRecover
* @method handleTabChange
* @method renderHelperComponents
* @method renderMainUI
* @method render
*
* @author Armin Eslami
* @since File available since release 1.0.0 (1396/6/8)
* @version 1.0.0
*
*/
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { hashHistory } from 'react-router';
import cookie from 'react-cookie';

import LoginUI from './LoginUI';
import { login, recover } from 'api/Board';
import { Global as GlobalStrings, Login as LoginStrings } from 'i18n/Persian';
import { updateApp } from 'redux/actions/AppActions';
import { updateUser } from 'redux/actions/UserActions';
import { validateEmail } from 'helpers/Helpers';

export class Login extends Component {
  constructor(props) {
    super(props);

    // Milliseconds for delaying component change
    this.TRANSITION_DELAY = 1000;
    // Min length for user inputs
    this.USERNAME_MIN_LENGTH = 1; // TODO : change this to 10
    this.PASSWORD_MIN_LENGTH = 1; // TODO : change this to 8
    // Days to save cookie
    this.COOKIE_EXPIRE_DAYS = 30; // REMOVE : No need to this after cookie is set by server

    // Component default states
    this.DEFAULT_STATES = {
      rootClassName: 'bottom-to-top-move-in', // To hold the class name of login's root element
      loginButtonClassNanme: 'fade-in-forwards-with-delay',
      isRecovering: false, // If true, then password recover inputs will be shown
      username: '', // To hold username field input
      password: '', // To hold password field input
      email: '', // To hold email field input
      isEmailValid: false, // To track if entered email is valid or not
      rememberMe: false, // To track whether user aks for being remembered or not
      showPassword: false, // To track whether password input should show its value or not
      windowWidth: undefined, // To track client's browser window width
      isLoading: false, // To track whether component is busy and doing tasks or not
      isSnackOpen: false, // To track whether snack is opened or not
      snackMessage: '', // To hold snack message
      autoLogOuted: false // If true, it means that user has been log outed automatically for being idle
    };

    // Changing these data wont re-render the components
    this.data = {
      usernameMinLength: this.USERNAME_MIN_LENGTH,
      passwordMinLength: this.PASSWORD_MIN_LENGTH
    };

    // Handlers for interacting with ui
    this.handlers = {
      onUsernameChange: this.handleUsernameChange,
      onPasswordChange: this.handlePasswordChange,
      onEmailChange: this.handleEmailChange,
      onRememberMeChanged: this.handleRememberMeChanged,
      onPasswordShowToggleTouchTap: this.handlePasswordShowToggleTouchTap,
      onSnackClose: this.handleSnackClose,
      onSectionToggleTouchTap: this.handleSectionToggleTouchTap,
      onAutoLogOutDialogClose: this.handleAutoLogOutDialogClose,
      onSubmit: this.handleSubmit
    };

    this.state = this.DEFAULT_STATES;

    // A listener for client's browser window size change
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
  }

  componentWillUpdate(nextProps, nextState) {
    process.env.NODE_ENV === 'development'
      ? console.log('New Component states =>', nextState)
      : void 0;
  }

  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener('resize', this.updateWindowDimensions);

    if (this.state.autoLogOuted !== this.props.app.autoLogOuted) {
      this.setState({
        autoLogOuted: this.props.app.autoLogOuted,
        username: '',
        password: ''
      });
    }
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateWindowDimensions);
  }

  /**
     * @description : This is called when client's browser width or height is changing
    *
    * @author Armin Eslami
    */
  updateWindowDimensions() {
    this.setState({ windowWidth: window.innerWidth });
  }

  render() {
    return (
      <LoginUI states={this.state} data={this.data} handlers={this.handlers} />
    );
  }

  /**
    * @description : Handler for username filed data change
    *
    * @author Armin Eslami
    *
    * @param username (string) : Input of username's field
    */
  handleUsernameChange = username => {
    this.setState({ username });
  };

  /**
    * @description : Handler for password filed data change
    *
    * @author Armin Eslami
    *
    * @param password (string) : Input of password's field
    */
  handlePasswordChange = password => {
    this.setState({ password });
  };

  /**
    * @description : Handler for email filed data change
    *
    * @author Armin Eslami
    *
    * @param email (string) : Input of email's field
    */
  handleEmailChange = email => {
    this.setState({ email, isEmailValid: validateEmail(email) });
  };

  /**
    * @description : Handler for remember me checkbox value change
    *
    * @author Armin Eslami
    *
    * @param rememberMe (bool) : State of checkbox which if its true, checkbox will be checked
    */
  handleRememberMeChanged = rememberMe => {
    this.setState({ rememberMe });
  };

  /**
    * @description : Handler for toggiling password filed state between showing or hiding it's input
    *
    * @author Armin Eslami
    */
  handlePasswordShowToggleTouchTap = () => {
    this.setState({ showPassword: !this.state.showPassword });
  };

  /**
    * @description : Handler for closing snack
    *
    * @author Armin Eslami
    */
  handleSnackClose = () => {
    this.setState({ isSnackOpen: false });
  };

  /**
    * @description : Handler for touching password recover section
    *
    * @author Armin Eslami
    */
  handleSectionToggleTouchTap = () => {
    this.setState({
      isRecovering: !this.state.isRecovering,
      username: '',
      password: '',
      email: '',
      rememberMe: false,
      showPassword: false
    });
  };

  /**
    * @description : Handler for closing auto log out dialog
    *
    * @author Armin Eslami
    */
  handleAutoLogOutDialogClose = () => {
    this.props.dispatch(updateApp({autoLogOuted:false}));
    this.setState({autoLogOuted:false});
  };

  /**
    * @description : Handler tapping on submit button or 
    * when user hits enter on one of the username, password or email inputs
    *
    * @author Armin Eslami
    */
  handleSubmit = () => {
    if (!this.state.isRecovering) {
      this.checkLoginInputs();
    } else {
      this.checkRecoverInputs();
    }
  };

  /**
    * @description : Checks login inputs and if they are all OK, calls login @function
    *
    * @author Armin Eslami
    */
  checkLoginInputs() {
    // Saving needed values
    const username = this.state.username;
    const password = this.state.password;
    const rememberMe = this.state.rememberMe;
    if (username.length === 0 && password.length === 0) {
      this.setState({
        snackMessage: LoginStrings.fillLoginInputs,
        isSnackOpen: true
      });
    } else if (username.length < this.USERNAME_MIN_LENGTH) {
      this.setState({
        snackMessage: LoginStrings.wrongUsernameInput.replace(
          'x',
          this.USERNAME_MIN_LENGTH
        ),
        isSnackOpen: true
      });
    } else if (password.length < this.PASSWORD_MIN_LENGTH) {
      this.setState({
        snackMessage: LoginStrings.wrongPasswordInput.replace(
          'x',
          this.PASSWORD_MIN_LENGTH
        ),
        isSnackOpen: true
      });
    } else {
      this.setState({ isLoading: true, isSnackOpen: false });
      this.login(username, password, rememberMe);
    }
  }

  /**
    * @description : Checks recover inputs and if they are all OK, calls recover @function
    *
    * @author Armin Eslami
    */
  checkRecoverInputs() {
    // Saving needed values
    const username = this.state.username;
    const email = this.state.email;
    if (username.length === 0 && email.length === 0) {
      this.setState({
        snackMessage: LoginStrings.fillRecoverInputs,
        isSnackOpen: true
      });
    } else if (username.length < this.USERNAME_MIN_LENGTH) {
      this.setState({
        snackMessage: LoginStrings.wrongUsernameInput.replace(
          'x',
          this.USERNAME_MIN_LENGTH
        ),
        isSnackOpen: true
      });
    } else if (!this.state.isEmailValid) {
      this.setState({
        snackMessage: LoginStrings.enterValidEmail,
        isSnackOpen: true
      });
    } else {
      this.setState({ isLoading: true, isSnackOpen: false });
      this.recover(username, email);
    }
  }

  /**
    * @description : Takes user's auth data and starts a login request
    *
    * @author Armin Eslami
    *
    * @param username (string) : User's username
    * @param password (string) : User's password
    * @param rememberMe (bool) : If true, user is asked to being remembered on next visit
    */
  login(username, password, rememberMe) {
    login(username, password, rememberMe).then(
      res => {
        process.env.NODE_ENV === 'development'
          ? console.log('login() is done with result :', res)
          : void 0;

        if (
          res.code === 200 &&
          res.data &&
          res.data.user &&
          res.data.user.universities
        ) {
          var token = res.data.token ? res.data.token : null;
          var uData = res.data.user;

          this.props.dispatch(updateUser({ token, ...uData, rememberMe }));

          // Now lets see how many universities is available for this user
          if (res.data.user.universities.length === 1) {
            // REMOVE: Because server should set the cookie if the remember me is checked
            // Or server should return the cookie token with the other data too
            if (rememberMe) {
              var date = new Date();
              date.setTime(
                date.getTime() + this.COOKIE_EXPIRE_DAYS * 24 * 60 * 60 * 1000
              );
              cookie.save('_wr', 'COOKIE_TOKEN', { path: '/', expires: date });
            }

            this.setState({ isLoading: false });
            this.props.dispatch(updateUser({ isLoggedIn: true }));
          } else if (res.data.user.universities.length > 1) {
            // User is registred on more than one university or has more than on type on the same university
            this.setState({ rootClassName: 'right-to-left-move-out' });
            setTimeout(() => {
              //TODO : change hashHistory to browserHistory
              hashHistory.replace('/accounts');
            }, this.TRANSITION_DELAY);
          }
        } else if (res.code === 400 && res.errors && res.errors[0].message) {
          this.setState({
            snackMessage: res.errors[0].message,
            isSnackOpen: true,
            isLoading: false
          });
        } else {
          this.setState({
            snackMessage: GlobalStrings.fatalError,
            isSnackOpen: true,
            isLoading: false
          });
        }
      },
      err => {
        process.env.NODE_ENV === 'development'
          ? console.log('login() failed with error :', err)
          : void 0;

        var errorMessage = GlobalStrings.serverError;

        if (err.code === 400 && err.errors && err.errors[0].message) {
          errorMessage = err.errors[0].message;
        } else if (err.code === 500 && err.message) {
          errorMessage = err.message;
        }

        this.setState({
          snackMessage: errorMessage,
          isSnackOpen: true,
          isLoading: false
        });
      }
    );
  }

  /**
    * @description : Takes user's data and starts a recover request
    *
    * @author Armin Eslami
    *
    * @param username (string) : User's username
    * @param email (string) : User's email
    */
  recover(username, email) {
    recover(username, email).then(
      res => {
        process.env.NODE_ENV === 'development'
          ? console.log('recover() is done with result :', res)
          : void 0;

        this.setState({ isLoading: false });
        if (res.code === 200) {
          this.setState({
            snackMessage: LoginStrings.recoverDone,
            isSnackOpen: true,
            username: '',
            email: '',
            isEmailValid: false
          });
        } else if (res.code === 400 && res.errors && res.errors[0].message) {
          this.setState({
            snackMessage: res.errors[0].message,
            isSnackOpen: true
          });
        } else {
          this.setState({
            snackMessage: GlobalStrings.fatalError,
            isSnackOpen: true
          });
        }
      },
      err => {
        process.env.NODE_ENV === 'development'
          ? console.log('login() failed with error :', err)
          : void 0;

        var errorMessage = GlobalStrings.serverError;

        if (err.code === 400 && err.errors && err.errors[0].message) {
          errorMessage = err.errors[0].message;
        } else if (err.code === 500 && err.message) {
          errorMessage = err.message;
        }

        this.setState({
          snackMessage: errorMessage,
          isSnackOpen: true,
          isLoading: false
        });
      }
    );
  }
}

export default connect(state => {
  return {
    app: state.app,
    user: state.user
  };
})(Login);



